:root {

  --main-color: #3E3EF4;

  --white-color: #fff;

  --primary-font-color: #969CB6;
  --secondary-font-color: #F6F7FB;

  --accent-color: #FF9B9B;

}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

.App {
  display: grid;
  height: 800px;
  width: 75%;
  /* max-width: 75%; */
  margin: 0 0 0 0;
  height: 100vh;
  width: 100%;
  border: none;
  /* box-shadow: 0px 4px 8px rgba(0,0,0,0.02), 0px 8px 12px rgba(0,0,0,0.03); */
  grid-template-columns: 240px 3fr;
  grid-template-rows: auto 100px;
  grid-template-areas:
  "r m m m m m"
  "n f f f f f"
}

.child {
  padding: 10px;
}

                          /* MESSAGE */

.messageList {
  grid-area: m;
  background-color: var(--white-color);
  overflow-x: hidden;
  overflow-y: auto;
}

.message {
  width: 100%;
  padding: 5px;
}

.message-username {
  font-size: .8em;
  padding: 5px;
  color: var(--primary-font-color);
}

.message-text {
  background-color: var(--secondary-font-color);
  color: var(--primary-font-color);
  display: inline-block;
  border-radius: 10px;
  padding: 10px;
}

.sendMessageForm {
  grid-area: f;
  padding: 0;
  z-index: 200;
}

.sendMessageForm form {
  height: 100%;
}

.sendMessageForm input {  /* TODO: Change size for mobile */
  height: 100%;
  width: 100%;
  border: none;
  padding-left: 10px;
  font-size: 1.2em;
  background: var(--white-color);
  color: var(--primary-font-color);
  /* -webkit-box-shadow:0px -3px 6px rgba(0,0,0,0.026), 0px -4px 8px rgba(0,0,0,0.043);
  -moz-box-shadow:0px -3px 6px rgba(0,0,0,0.026), 0px -4px 8px rgba(0,0,0,0.043); */
  -webkit-box-shadow: 0px -3px 6px rgba(0,0,0,0.026), 0px -4px 8px rgba(0,0,0,0.043);
          box-shadow: 0px -3px 6px rgba(0,0,0,0.026), 0px -4px 8px rgba(0,0,0,0.043);
}
.sendMessageForm input::-webkit-input-placeholder{
  color: var(--primary-font-color);
}
.sendMessageForm input:-ms-input-placeholder{
  color: var(--primary-font-color);
}
.sendMessageForm input::-ms-input-placeholder{
  color: var(--primary-font-color);
}
.sendMessageForm input::placeholder{
  color: var(--primary-font-color);
}
.sendMessageForm input:focus {
  outline: none;
}

                              /* ROOMS */

.createRoomForm {
  grid-area: n;
  height: 100%;
  padding: 0;
  z-index: 200;
}

.createRoomForm form {
  height: 100%;
}

.createRoomForm input {
  background-color: var(--white-color);
  height: 100%;
  width: 100%;
  border: none;
  padding-left: 10px;
  font-size: 1.2em;
}
.createRoomForm input::-webkit-input-placeholder {
  color: var(--primary-font-color);
}
.createRoomForm input:-ms-input-placeholder {
  color: var(--primary-font-color);
}
.createRoomForm input::-ms-input-placeholder {
  color: var(--primary-font-color);
}
.createRoomForm input::placeholder {
  color: var(--primary-font-color);
}

.createRoomForm input:focus {
  outline: none;
}


.roomList {
  grid-area: r;
  background: -webkit-gradient(linear, left top, right top, color-stop(52%, var(--main-color)), color-stop(95%, rgba(94,94,208,1)));
  background: -webkit-linear-gradient(left, var(--main-color)  52%, rgba(94,94,208,1) 95%);
  background: -o-linear-gradient(left, var(--main-color)  52%, rgba(94,94,208,1) 95%);
  background: linear-gradient(90deg, var(--main-color)  52%, rgba(94,94,208,1) 95%);
  height: 100%;
  z-index: 200;
  -webkit-box-shadow: 3px 0px 6px rgba(0,0,0,0.026), 4px 0px 8px rgba(0,0,0,0.043);
          box-shadow: 3px 0px 6px rgba(0,0,0,0.026), 4px 0px 8px rgba(0,0,0,0.043);
}
.room-title {
  color: var(--white-color);
  font-weight: bold;
  padding: 5px;
  font-size: 20px;
}
.room {
  list-style: none;
  padding: 5px;
}
.room a {
  text-decoration: none;
  color: var(--primary-font-color);
  font-weight: bold;
}

.active a {
  color: var(--white-color);
}

                          /* MOBILE NAVIGATION */

.navBar {
  display: none;
  grid-area: b;
  height: 10px;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(46%, rgba(62,62,244,1)), to(rgba(88,88,254,1)));
  background: -webkit-linear-gradient(top, rgba(62,62,244,1) 46%, rgba(88,88,254,1) 100%);
  background: -o-linear-gradient(top, rgba(62,62,244,1) 46%, rgba(88,88,254,1) 100%);
  background: linear-gradient(180deg, rgba(62,62,244,1) 46%, rgba(88,88,254,1) 100%);
  color: var(--white-color);
  z-index: 200;
  /* -webkit-box-shadow:0px 3px 6px rgba(0,0,0,0.026), 0px 4px 8px rgba(0,0,0,0.043);
  -moz-box-shadow:0px 3px 6px rgba(0,0,0,0.026), 0px 4px 8px rgba(0,0,0,0.043); */
  -webkit-box-shadow: 0px 3px 6px rgba(0,0,0,0.016), 0px 4px 8px rgba(0,0,0,0.18);
          box-shadow: 0px 3px 6px rgba(0,0,0,0.016), 0px 4px 8px rgba(0,0,0,0.18);
}

.navBar ul {
  margin: 0;
  list-style: none;
}

.navBar li {
  display: inline-block;
  padding: 5px 10px;
}
.navBar i {
  cursor: pointer;
}

.toggleRoomMenuButton {
  display: inline-block;
  height: 20px;
  width: 20px;
  border: none;
  cursor: pointer;
  font-size: 1.2em;
  color: var(--white-color);
  background: none;
}
.toggleRoomMenuButton:focus {
  outline: none;
}

.sideDrawer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  height: 100%;
  background-color: var(--white-color);
  -webkit-box-shadow: 2px 6px 12px rgba(0,0,0,0.15);
          box-shadow: 2px 6px 12px rgba(0,0,0,0.15);
  position: fixed;
  top: 0;
  left: 0;
  max-width: 300px;
  width: 60%;
  z-index: 240;
  -webkit-transform: translateX(-110%);
      -ms-transform: translateX(-110%);
          transform: translateX(-110%);
  -webkit-transition: -webkit-transform 450ms ease-in-out;
  transition: -webkit-transform 450ms ease-in-out;
  -o-transition: transform 450ms ease-in-out;
  transition: transform 450ms ease-in-out;
  transition: transform 450ms ease-in-out, -webkit-transform 450ms ease-in-out;
}
.sideDrawer.open {
  -webkit-transform: translateX(0%);
      -ms-transform: translateX(0%);
          transform: translateX(0%);
}

.backdrop {
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.3);
  z-index: 210;
}

.mobileRoomList .room-title {
  color: var(--main-color);
}

.mobileRoomList li a{
  color: var(--primary-font-color);
}

.mobileRoomList .active a {
  /* color: var(--primary-font-color); */
}

.mobileCreateRoomForm  {
  height: 70px;
  padding: 0;
}

.mobileCreateRoomForm input {
  padding: 0 0 0 5px;
  font-size: 1em;
  height: 100%;
  width: 100%;
  border: none;
  background-color: var(--secondary-font-color);
  color: var(--primary-font-color);
}
.mobileCreateRoomForm input::-webkit-input-placeholder {
  color: var(--primary-font-color);
}
.mobileCreateRoomForm input:-ms-input-placeholder {
  color: var(--primary-font-color);
}
.mobileCreateRoomForm input::-ms-input-placeholder {
  color: var(--primary-font-color);
}
.mobileCreateRoomForm input::placeholder {
  color: var(--primary-font-color);
}


                              /* MEDIA QUERIES */


@media (max-width: 950px) {
  .App {
    margin: 0 0 0 0;
    height: 100vh;
    width: 100%;
    border: none;
  }
}

@media (max-height: 849px) {
  .App {
    margin: 0 auto;
    height: 100vh;
  }
}

@media (max-width: 750px) {
  .App {
    grid-template-rows: 50px 1fr 80px;
    grid-template-areas:
    "b b b b b b"
    "m m m m m m"
    "f f f f f f"
  }

  .roomList {
    display: none;
  }

  .navBar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    grid-area: b;
    height: 100%;
    padding: 0 5px;
  }

  .createRoomForm input {
    display: none;
  }
}



