body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#root {
  height: 100%;
}

html {
  height: 100%;
  background: -webkit-radial-gradient(circle, rgba(255,255,255,1) 43%, rgba(246,247,251,1) 57%) !important;
  background: -o-radial-gradient(circle, rgba(255,255,255,1) 43%, rgba(246,247,251,1) 57%) !important;
  background: radial-gradient(circle, rgba(255,255,255,1) 43%, rgba(246,247,251,1) 57%) !important;
}



